import React, { useState } from "react";
import { Button, Col, Container, Form, Row } from "react-bootstrap";
import { APIUsers } from "../../api/api";
import { Alerts } from "../../components/FormUtilities";
import styles from "./RegisterPage.module.css";

const RegisterPage = () => {
  const [user, setUser] = useState({
    nombre: "",
    apellidos: "",
    email: "",
    telefono: "",
    contrase単a: "",
    confirmarContrase単a: "",
  });

  const [alert, setAlert] = useState();
  const [alertMsg, setAlertMsg] = useState();
  const [alertVariant, setAlertVariant] = useState();

  const handleAlert = (msg, variant) => {
    setAlert(true);
    setAlertMsg(msg);
    setAlertVariant(variant);
    setTimeout(() => {
      setAlert(false);
      setAlertMsg("");
      setAlertVariant("");
    }, 3000);
  };

  const handleChange = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  const hasWhiteSpace = () => {
    return /\s/g.test(user.contrase単a);
  };

  const sendRequest = async () => {
    const res = await APIUsers.createUser({
      nombre: user.nombre,
      apellidos: user.apellidos,
      email: user.email,
      telefono: user.telefono,
      contrase単a: user.contrase単a,
      confirmarContrase単a: user.confirmarContrase単a,
    });
    if (res[1].status === 400) {
      handleAlert(res[0]["message"], "danger");
    } else {
      cleanInputs();
      handleAlert(res[0]["Response"], "success");
    }
  };

  const cleanInputs = () => {
    setUser({
      nombre: "",
      apellidos: "",
      email: "",
      telefono: "",
      contrase単a: "",
      confirmarContrase単a: "",
    });
  };

  const verifyPassword = () => {
    if (hasWhiteSpace()) {
      handleAlert("La contrase単a no debe llevar espacios en blanco", "danger");
      return;
    } else {
      if (user.contrase単a !== user.confirmarContrase単a) {
        handleAlert("La contrase単as ingresadas no coinciden", "danger");
        return;
      } else {
        sendRequest();
      }
    }
  };

  const verifyInputs = () => {
    for (const info in user) {
      if (user[info] === "") {
        handleAlert("No puedes dejar espacios en blanco", "danger");
        return;
      }
    }
    const phone = parseInt(user.telefono);
    if (isNaN(phone) || user.telefono.length !== 10) {
      handleAlert(
        "El tel辿fono est叩 incorrecto, debe ser un n炭mero de 10 caracteres",
        "danger"
      );
      return;
    }

    verifyPassword();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    verifyInputs();
  };

  return (
    <Container fluid className={`${styles.container} pt-4`}>
      <h1 className={styles.title}>Crear Cuenta</h1>
      <Form className={`${styles.form}`} onSubmit={handleSubmit}>
        {alert ? Alerts(alertMsg, alertVariant, setAlert) : null}
        <h2 className={styles.sectionSubtitle}>Datos Personales</h2>
        <Row>
          <Col className="col-md-6 col-12">
            <Form.Group controlId="formNombre">
              <Form.Label>Nombre</Form.Label>
              <Form.Control
                type="text"
                name="nombre"
                placeholder="Ingresa tu nombre"
                className={styles.input}
                value={user.nombre}
                onChange={handleChange}
              />
            </Form.Group>
          </Col>
          <Col className="col-md-6 col-12">
            <Form.Group controlId="formApellidos">
              <Form.Label>Apellidos</Form.Label>
              <Form.Control
                type="text"
                name="apellidos"
                placeholder="Ingresa tus apellidos"
                className={styles.input}
                value={user.apellidos}
                onChange={handleChange}
                isValid={false}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col className="col-12 col-md-6">
            <Form.Group controlId="formEmail">
              <Form.Label>Correo electr坦nico</Form.Label>
              <Form.Control
                type="text"
                name="email"
                placeholder="Ingresa tu correo electr坦nico"
                className={styles.input}
                value={user.email}
                onChange={handleChange}
              />
            </Form.Group>
          </Col>
          <Col>
            <Form.Group controlId="formTelefono">
              <Form.Label>N炭mero de Tel辿fono</Form.Label>
              <Form.Control
                type="text"
                name="telefono"
                placeholder="Ingresa tu n炭mero telefonico"
                className={styles.input}
                value={user.telefono}
                onChange={handleChange}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col className="col-12 col-md-6">
            <Form.Group controlId="formContrase単a">
              <Form.Label>Contrase単a</Form.Label>
              <Form.Control
                type="password"
                name="contrase単a"
                placeholder="Contrase単a"
                className={styles.input}
                value={user.contrase単a}
                onChange={handleChange}
              />
            </Form.Group>
          </Col>
          <Col>
            <Form.Group controlId="formConfirmarContrase単a">
              <Form.Label>Confirmar Contrase単a</Form.Label>
              <Form.Control
                type="password"
                name="confirmarContrase単a"
                placeholder="Confirmar contrase単a"
                className={styles.input}
                value={user.confirmarContrase単a}
                onChange={handleChange}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col className="d-flex justify-content-end mt-3">
            <Button variant="danger" className="mr-3" size="lg">
              Cancelar
            </Button>
            <Button type="submit" variant="primary" size="lg">
              Registrar
            </Button>
          </Col>
        </Row>
      </Form>
    </Container>
  );
};

export default RegisterPage;
